#recent-projects .combined .screen.iphone {
  position: absolute;
  top: 14.5%;
  left: -3%;
  width: 33%;
}
#recent-projects .combined .screen.iphone.right {
  left: auto;
  right: -3%;
}
#recent-projects .combined .screen.iphone:before {
  margin-top: 0;
  margin-bottom: 0;
}
@media screen and (max-width: 1024px) {
  #recent-projects .combined .screen.iphone {
