  margin-top: 72.5px;
  margin-top: 3.625rem;
}
@media screen and (max-width: 414px) {
  #recent-projects .combined .screen.ipad {
    display: none;
  }
  #recent-projects .combined .screen.iphone {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    width: 66%;
    margin: 30% auto 30%;
  }
}
#recent-projects .combined + footer {
  margin-top: 87px;
  margin-top: 4.35rem;
}
#recent-projects .double {
  max-width: 1024px;
  margin-left: -4.5%;
  margin-right: -4.5%;
}
#recent-projects .double > .concept {
  clear: none;
  max-width: none;
  margin-left: 0;
  margin-right: 0;
  float: left;
  width: 47.87234043%;
  width: calc(50% - 20px);
}
