    margin-left: -2.25%;
    margin-right: -2.25%;
  }
}
#recent-projects .screen {
  transition: opacity 100ms 100ms, transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
#recent-projects .playing .screen.iphone.static {
  transform: translateX(-150%);
  transition-delay: 300ms, 0ms;
  opacity: 0;
}
#recent-projects .playing .screen.iphone.static.right {
  transform: translateX(150%);
}
